
.about__container{
    display:grid;
    grid-template-columns: 35% 50%;
    gap: 15%;
    place-items:center;
    
    

}
.aboutheader{
    display:grid;
    place-items:center;
    margin-right: 2rem;
}

.about__me{
    width:100%;
    height:75%;
    aspect-ratio:1/1;
    border-radius: 2rem;
    /* background: linear-gradient(
        45deg, transparent, var(--color-primary),
        transparent);
    background-size: contain;
    background-position: center; */
    display:grid;
    place-items:center;
    margin-top: auto;

}

.about__me-image{
    border-radius: 1rem;
    overflow: hidden;
    transform:rotate(0deg);
    transition: var(--transition);
    max-width: 90%;
    height: auto;
    

}

.about__me-image:hover{
    transform: rotate(0);
}

 .about__cards{
   display:grid;
   grid-template-columns: repeat(2,1fr);
   gap:2rem;
   margin-top: 3rem;
   margin-bottom: 2rem;

}

.about__card{
    background: var(--color-bg-variant);
    border:1px solid transparent;
    border-radius: 3rem;
    padding: 1rem;
    text-align: center;
    transition: var(--transition);
}

.about__card:hover{
    background: transparent;
    border-color: var(--color-primary-variant);
    cursor: default;
}

.about__icon{
    color: var(--color-primary);
    font-size: 1.4rem;
    margin-bottom:0rem ;

}

.about__card h5{
    font-size: 1rem;

}

.about__card small{
    font-size: 0.7rem;
    color:var(--color-white);
}

.about__cotent p{
    margin: 2rem 0 2.6em;
    color: var(--color-white);
}

.paragraph {
    margin-bottom: 2rem;
}
@media screen and (max-width:1024px){
    .about__container{
        grid-template-columns: 1fr;
        gap: 0;
    }
    .about__me{
        width:50%;
        margin: 2rem auto 4rem;
    }

    .about__content p{
        margin: 1rem 0 1.5rem;
    }
}
 

 @media screen and (max-width:600px){
    .about__me{
        width:65%;
        margin: 0 auto 3rem;
    }
    
    .about__cards{
        grid-template-columns: 1fr 1fr;
        gap: 1rem;
    }

    .about__content{
        text-align: center;

    }
    .about__content p{
        margin: 1.5rem 0;
    }
 }